export default {
	data() {
		return {
			collectWordList: []
		}
	},
	methods: {
		// 返回
		goBack() {
			uni.navigateBack({
				delta: 1
			});
		},
		// 获取全部收藏
		getCollectWordsList() {
			this.$minApi.getCollectWordsList({
				studentId: getApp().globalData.studentId
			}).then(data => {
				console.log(data)
				this.collectWordList = data.data
			})
		}

	},
	created() {
		this.getCollectWordsList()
	}

}
